#navbar{
    -ms-flex: 1 1;
        flex: 1 1;
    -ms-flex-align: center;
        align-items: center;
    text-transform: uppercase;
    font-size: 1.5vh;
    display: -ms-flexbox;
    display: flex;
    color: white;
    cursor: pointer;
}

#navbar > div{
    -ms-flex: 1 1;
        flex: 1 1;
    border-bottom: solid 4px rgba(0,0,0,0);
}

#navbar > div:hover{
    border-bottom: solid 4px rgb(50, 119, 146);
}

#navbar > div > span{
    font-size: 1em;
    margin: 5px;
}




