#portfolio{
    width: 80vw;
    margin-top: 10vh;
    margin: 0 auto;
    color: white;
}

#portfolioTitle{
    font-size: 10vh;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    color: white;
    margin: 0 auto;
    display: -ms-flexbox;
    display: flex;
}

#portfolioTitle > span {
    -ms-flex: 2 1;
        flex: 2 1;
    font-size: 7vh;
    color: #224e69;
}

#showAll{
    font-size: 3vh;
    color: white;
    height: 40px;
    width: 45px;
    border: solid 2px black;
    background-color: #224e69;
}

#showAll:hover{
    background-color: #4a738a;
}

#projectDivs{
    display: -ms-flexbox;
    display: flex; 
    -ms-flex-direction: row; 
        flex-direction: row;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

#iconDiv{
    -ms-flex: 1 1;
        flex: 1 1;
    max-width: 140px;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-line-pack: start;
        align-content: start;
    font-size: 24px;
}



.icons{
    -ms-flex: 1 1;
        flex: 1 1;
    border: solid 1px black;
    height: 30px;
    max-width: 30px;
    min-width: 30px;
    margin-left: 2px;
    margin-top: 2px;
}

.icons.active{
    background-color: rgb(56, 54, 54);
}


